.contactModule{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-around;
}
.container {
    width: 380px;
    height: 200px;
    border: 1px solid #282c34;
    border-radius: 5px;
    display: flex;
    align-items: center;
    margin: 10px;
    background-color: rgba(116, 120, 122, 0.24);
    position: relative;
}
.delete{
    position: absolute;
    top: 5px;
    left: 10px;

}
.photo {
    width: 80px;
    height: 80px;
    background-size: cover;
    margin-left: 5px;
    border-radius: 50%;
    padding: 20px;
}
.infoContact {
    width: 75%;

}
.infoContact {
    padding: 3px;
    width: 100%;
}

.data {
    display: flex;
    justify-content: flex-end;
    margin: 10px 0;
}

.infoName {
    text-align: center;

}

.info {
    border: 1px solid black;
    width: 70%;
    text-align: left;
    padding-left: 5px;
    margin-left: 5px;
}